* {
    border: 0;
    margin: 0;
    box-sizing: border-box;

}

#demo {
    word-break: break-all;
}

#div1 {
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    top: 0px;
}

#div1::before {
    content: '';
    display: block;
    /* border: 1px solid red; */
    position: absolute;
}

#div1::after {
    content: '';
    display: block;
    /* border: 1px solid red; */
    position: absolute;
}

@media (max-width:500px) {
    #html {
        height: 50vh;
        word-break: break-all;
        overflow: auto;
    }

    #div1wrapper {
        height: 50vh;
    }

    #div1 {
        position: relative;
    }
}